import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, Input, OnInit } from '@angular/core';
import { ExpansionArrayModel } from 'src/app/core/models/expansion-helper-array.model';

@Component({
  selector: 'app-expansion-helper',
  templateUrl: './expansion-helper.component.html',
  styleUrls: ['./expansion-helper.component.scss']
})
export class ExpansionHelperComponent implements OnInit {
  @Input() expansionArrayModel:ExpansionArrayModel;
  formGroupTamanos:FormGroup[];
  constructor(private fb:FormBuilder) { }

  ngOnInit(): void {
    if(this.expansionArrayModel && this.expansionArrayModel.tipo==1){//ExpansionPanel del tipo Tamano
      if(this.expansionArrayModel.arrayTamano.length>0){
        for(let tamano of this.expansionArrayModel.arrayTamano){
          let formGroupTamano:FormGroup = this.fb.group({
            titulo:[tamano.titulo, Validators.required],
            descripcion:[tamano.descripcion],
            precio:['', Validators.required],
            imagen:[''],
            imagenUrl:[''],
            propiedades: this.fb.group({
              cantComensales:[''],
              cantPorciones:[''],
              pesoEnGr:['']
            })

          })
        }
      }
    }
  }

}
